import Vue from 'vue'
import VueRouter from 'vue-router'
// import Login from './components/Login.vue'
const Login = () => import(/* webpackChunkName: "login_home_welcome" */ '../components/Login.vue')
// import Home from './components/Home.vue'
const Home = () => import(/* webpackChunkName: "login_home_welcome" */ '../components/Home.vue')
// import Welcome from './components/Welcome.vue'
const Welcome = () => import(/* webpackChunkName: "login_home_welcome" */ '../components/Welcome.vue')

// import Users from './components/user/Users.vue'
const User = () => import(/* webpackChunkName: "Users_Rights_Roles" */ '../components/user/User.vue')
// import Rights from './components/power/Rights.vue'
const Rights = () => import(/* webpackChunkName: "Users_Rights_Roles" */ '../components/Rights.vue')
// import Roles from './components/power/Roles.vue'
const Roles = () => import(/* webpackChunkName: "Users_Rights_Roles" */ '../components/Roles.vue')

// import Cate from './components/goods/Cate.vue'
const Cate = () => import(/* webpackChunkName: "Cate_Params" */ '../components/goods/Cate.vue')
// import Params from './components/goods/Params.vue'
const Params = () => import(/* webpackChunkName: "Cate_Params" */ '../components/goods/Params.vue')

// import GoodsList from './components/goods/List.vue'
const Goods_list = () => import(/* webpackChunkName: "GoodsList_Add" */ '../components/goods/Goods_list.vue')
// import Add from './components/goods/Add.vue'
const Goods_add = () => import(/* webpackChunkName: "GoodsList_Add" */ '../components/goods/Goods_add.vue')

// import Order from './components/order/Order.vue'
const Order = () => import(/* webpackChunkName: "Order_Report" */ '../components/order/Order.vue')
// import Report from './components/report/Report.vue'
const Report = () => import(/* webpackChunkName: "Order_Report" */ '../components/report/Report.vue')


Vue.use(VueRouter)

const routes = [
  //设置首页重定向到登录页面
  {
    path: '/',
    redirect: '/login'
  },
  // 添加登录路由
  {
    path: '/login',
    component: Login
  },
  //添加home路由
  {
    path: '/home',
    component: Home,
    redirect: '/welcome',//重定向到welcome
    children: [
      { path: '/welcome', component: Welcome },
      { path: '/users', component: User },
      { path: '/rights', component: Rights },
      { path: '/roles', component: Roles },
      { path: '/categories', component: Cate },
      { path: '/params', component: Params },
      { path: '/goods', component: Goods_list },
      { path: '/goods/adds', component: Goods_add },
      { path: '/orders', component: Order },
      {path:'/reports',component:Report}
    ]

  },
  //重定向到Welcome组件

]

const router = new VueRouter({
  routes
})
//为router添加导航守卫
router.beforeEach((to, from, next) => {
  //to代表要跳转的路径
  //from代表从哪个路径跳转而来
  //next代表函数，表示放行
  //判断跳转路径是否为login，若为login则放行
  if (to.path === '/login') return next();
  //获取客户端token
  var tokenStr = window.sessionStorage.getItem('token');
  //判断token是否存在,若为空则跳转到login
  if (!tokenStr) return next('/login');
  next();
})

export default router
